import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import CodeSnippet from "../components/code_snippet";

import cs_array_dec from "../images/array_declaration.png";
import cs_array_write from "../images/array_write.png";
import cs_array_read from "../images/array_read.png";

const COfCodeDataStructuresArray = () => (
    <Layout>
        <SEO title="C of Code Arrays" />
        <Link to="/">Home </Link>
        <Link to="/cofcode/">/ C Of Code </Link>
        <Link to="/coc_datastructures/">/ Data Structures </Link>
        <h1>C Arrays</h1>
        <p>
            An Array is the simplest data structure in C and most other languages, it is a block of continuous memory which is reserved for storing values of the same type.
        </p>
        <p>
            <h2>The key features of an array are:</h2>
            <p>
                <ul>
                    <li>Each element in the array is stored directly after the previous element in memory</li>
                    <li>Array indexes start at 0</li>
                    <li>Each element in the array must be the same type e.g. an array of integers</li>
                    <li>Arrays form the basis of many of the more complicated data structures</li>
                </ul>
            </p>
        </p>
        <p>
            <h1>Using Arrays</h1>
            <h3>Declaring an array</h3>

            Arrays are declared using square brackets [ ] as shown in the following code snippet:

            <CodeSnippet image={cs_array_dec} title="Array declaration example" />

            <ul>
                <li>The value inside the square brackets [ ] defines the number of elements in the array. In the above example there are 10 elements as defined by NUMBER_OF_ARRAY_ELEMENTS</li>
                <li>the indexes to this array will range from 0 to 9 (NUMBER_OF_ARRAY_ELEMENTS - 1).</li>
                <li>if no initial values are declared, as in the uninitialised_array example, the initial values may be whatever was previously in memory and are not guaranteed to be 0</li>
                <li>therefore it is preferable to initialise arrays at decleration or set the inintial array values in an initialisation function.</li>
            </ul>

            <h3>Writing values</h3>

            Writing to an array is done by setting the value of an element to a new value as shown in the following code snippet:

            <CodeSnippet image={cs_array_write} title="Array element value writing example" />

            <ul>
                <li>Most compilers don't provide any compile time checking of the index used to write to the array</li>
                <li></li>
            </ul>

            <h3>Reading values</h3>

            Reading from an array is similar to writing to an array, as shown in the following code snippet:

            <CodeSnippet image={cs_array_read} title="Array element value reading example" />
        </p>
    </Layout >
)

export default COfCodeDataStructuresArray
