import React from "react"

const CodeSnippet = props => {

  return <div style={{
    display: 'flex',
    justifyContent: 'center',
    margin: '20px',
  }}>
    <div style={{
      borderStyle: 'solid',
      borderWidth: '1px 1px 3px 4px',
      borderColor: 'green',
      borderRadius: '0px 10px 0px 10px'
    }}>
      <h5 style={{ textAlign: 'center' }}>Code snippet: {props.title}</h5>
      <img src={props.image} alt={props.title} />
    </div>
  </div>
}

export default CodeSnippet
